import React from 'react';

import './footnotes.css';


function Footnotes({children}) {
    return <section className='footnotes'>
        <hr/>
        <h2>Footnotes</h2>
        <ol>
            {children}
        </ol>
    </section>;
}

function Footnote({children, id}) {
    return <li>
        <span className='label'>[{id}]</span>
        {children}
        <a className='reference' href={`#reference-${id}`}>â†©</a>
        <div className='anchor' id={`footnote-${id}`}/>
    </li>;
}

function Ref({id}) {
    return <a
        className='reference'
        href={`#footnote-${id}`}
    >
        <sup>[{id}]</sup>
        <div className='anchor' id={`reference-${id}`}/>
    </a>;
}

export {Footnotes, Footnote, Ref};
